@import 'normalize.css';
$background-color:#d2dae2;
$textColor: #3742fa;
$drumPadWidth: 80px;
$drumPadHeight: 80px;

* {
  box-sizing: border-box;
}

.container-outer {
  background-color: $background-color;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.container-inner {
  max-width: 720px;
  width: 100%;
  display: flex;
  border: 2px solid #808e9b;
  border-radius: 2px;

  .padBankContainer {
    padding: 5px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 5px;
  }

  .controller {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    color: $textColor;

    .power {
      user-select: none;
      &-outer {
        margin-top: 5px;
        width: 40px;
        height: auto;
        background-color: #f1f2f6;
        border-radius: 10px;
        border: 2px solid #a4b0be;
        display: flex;
        justify-content: flex-start;
      }

      &-inner {
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color:#e84118;
        cursor: pointer;
        transition: .2s;
        margin: .5px;
        transform: translateY(-.4px);
      }
    }

    .display {
      &-outer {
        margin-top: 5px;
        border-radius: 2px;
        width: 120px;
        height: auto;
        background-color: #a4b0be;
      }

      &-inner {
        color: black;
        text-align: center;
        padding: 5px;
      }
    }

    .bank {
      user-select: none;
      &-outer {
        @extend .power-outer;
      }

      &-inner {
        @extend .power-inner;
        transform: translateY(-.1px);
      }
    }
  }
}

.drumPad {
  width: $drumPadWidth;
  height: $drumPadHeight;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
}

.author {
  margin-top: 20px;
}